import React, { useState } from 'react';

import TabBtn from '../../components/TabBtn';
import {
  information,
} from './datas';
import styles from './index.less';
import ActiveInfo from '../../../../components/ActiveInfo';
import Box from '../../Boxs/Box';
import { UserState } from '../../model';
import { connect } from 'dva';

interface IProps {
  user: UserState;
}

const BasicData = ({ data }) => {
  return (
    <Box title="基础资料">
      <div>
        <ActiveInfo title="谱蓝定投" value={data?.wealthAccount?.isRegister ? '已注册' : ''} />
        <ActiveInfo title="是否开户" value={data?.wealthAccount?.isAccount ? '已开户' : ''} />
        <ActiveInfo title="谱蓝财富公众号" value={data?.wealthRecord?.isBindWeChat} />
        <ActiveInfo title="加好友时间" value={data?.wealthRecord?.addTime} />
        <ActiveInfo title="报名电话" value={data?.wealthRecord?.phone} />
        <ActiveInfo title="年龄" value={data?.wealthRecord?.age} />
        <ActiveInfo title="已关注公众号" value={data?.wealthRecord?.isBindWeChat} />
        <ActiveInfo title="是否标普500客户" value={data?.wealthStatus?.isSpxClient} />
        <ActiveInfo title="是否年金客户" value={data?.wealthStatus?.isAnnuityClient} />
        <ActiveInfo title="是否wealth客户" value={data?.wealthStatus?.isWealthClient} />
        <ActiveInfo title="备注" value='' />
      </div>
    </Box>
  )
}

const PlanInvestment = ({ data }) => {
  return (
    <Box title="谱蓝定投">
      <div>
        <ActiveInfo title="活期宝" value={data?.wealthRecord?.walletAmount} />
        <ActiveInfo title="货币三佳" value={data?.wealthRecord?.best3CoinPoAmount} />
        <ActiveInfo title="享定投" value={data?.wealthRecord?.myPoAmount} />
        <ActiveInfo title="稳稳的幸福" value={data?.wealthRecord?.happyPoAmount} />
        <ActiveInfo title="春华秋实" value={data?.wealthRecord?.flowerFruitPoAmount} />
        <ActiveInfo title="券商资管" value={data?.wealthRecord?.securitiesAmount} />
        <ActiveInfo title="短债组合" value={data?.wealthRecord?.shortDebtPoAmount} />
        <ActiveInfo title="自选" value={data?.wealthRecord?.fundAmount} />
      </div>
    </Box>
  )
}

const DataTabs: React.FC<IProps> = (props) => {
  const { user: { userData } } = props;
  console.log(props);


  const [type, setType] = useState<string>('');

  const handleClick = (value: string) => {
    setType(value)
  }

  return (
    <>
      <div className={styles.headerBox}>
        <TabBtn options={information} onChange={handleClick} />
      </div>
      {
        type ? type === 'basic_data' && <div className={styles.contentBox}>
          <BasicData data={userData.wealth} />
        </div> : <div className={styles.contentBox}>
            <BasicData data={userData.wealth} />
          </div>
      }
      {
        type ? type === 'plan_investment' && <div className={styles.contentBox}>
          <PlanInvestment data={userData.wealth} />
        </div> : <div className={styles.contentBox}>
            <PlanInvestment data={userData.wealth} />
          </div>
      }
    </>
  )
}
export default connect(({ user }: { user: UserState }) => ({
  user,
}))(DataTabs);