import React, { useEffect, useState } from 'react';

import TabBtn from '../../components/TabBtn';
import {
  Planpro_information,
} from './data';
import styles from './index.less';
import ActiveInfo from '../../../../components/ActiveInfo';
import Box from '../../Boxs/Box';
import { UserState } from '../../model';
import { connect } from 'dva';

import edit from '../../../../assets/icons/edit.svg';
import { DecryptDes, GetCookie } from '../../../../utils/func';

interface IProps {
  user: UserState;
  dispatch: Function,
}

const BasicData = ({ data }) => {
  return (
    <Box title="基础资料">
      <div>
        <ActiveInfo title="渠道来源" value={data?.channel} />
        <ActiveInfo title="加好友时间" value={data?.addTime} />
        <ActiveInfo title="报名电话" value={data?.feimooc.signupPhone} />
        <ActiveInfo title="已关注公众号" value={data?.subscribe} />
        <ActiveInfo title="企微描述" value={data?.description} />
        <ActiveInfo title="已报名的课程" value={data?.feimooc.inFirstInsure ? '小白营' : data?.feimooc.fixedInvestmentCamp ? '训练营' : ''} />
        <ActiveInfo title="所在城市" value={data?.pro.city} />
        <ActiveInfo title="客户年龄" value={data?.pro.age} />
        <ActiveInfo title="婚姻" value={data?.pro.maritalStatus} />
        <ActiveInfo title="子女数量" value={data?.pro.childNum} />
        <ActiveInfo title="家庭年收入" value={data?.pro.income} />
        <ActiveInfo title="年终结余" value={data?.pro.AnnualAssetsBegin} />
        <ActiveInfo title="流动资产" value={data?.pro.currentAssetsBegin} />
        <ActiveInfo title="意愿类别" value={data?.pro.staffCate} />
        <ActiveInfo title="预估FYP" value={data?.pro.estimatedFYP} />
        <ActiveInfo title="保单数量（小悦）" value={data?.pro.policyAmount} />
        <ActiveInfo title="保单数量（PlanPro）" value={data?.pro.policyNum} />
        <ActiveInfo title="报名来源" value={data?.pro.source} />
        <ActiveInfo title="方案匹配度" value={data?.pro.matching} />
      </div>
    </Box>
  )
}

const HealthQuestionnaire = () => {
  return (
    <Box title="健康问卷">
      <div>
        <ActiveInfo title="备注" value={
          <div>无<img src={edit} alt="编辑按钮" /></div>
        } />
        <div className={styles.trainWealthQuestionCard}>
          <img src="http://wework.qpic.cn/bizmail/iajiakxs31goYHIFkIRm7g3GaniaH878bwcZmIiaXFIxYibBTic7GJGExDSw/0" alt="健康问卷" className={styles.left} />
          <div className={styles.right}>
            <p>先生 | <span>1998-03-15</span></p>
            <ul>
              <li>身体状况良好</li>
            </ul>
          </div>
        </div>
      </div>
    </Box>
  )
}

const SecurityPlan = ({ data }) => {
  return (
    <Box title="保障规划">
      <div>
        <ActiveInfo title="保障顾问" value={data?.pro.baoConsultantCnName} />
        <ActiveInfo title="服务进度" value={data?.pro.GuaranteeData.ServiceSchedule} />
        <ActiveInfo title="沉默时长" value={data?.pro.GuaranteeData.silence} />
        <ActiveInfo title="回访状态" value={data?.pro.GuaranteeData.visit_status} />
      </div>
    </Box>
  )
}

const ComprehensivePlan = ({ data }) => {
  return (
    <Box title="综合规划">
      <div>
        <ActiveInfo title="综合顾问" value={data?.pro.consultantCnName} />
        <ActiveInfo title="服务进度" value={data?.pro.FinanceData.ServiceSchedule} />
        <ActiveInfo title="沉默时长" value={data?.pro.FinanceData.ServiceSchedule} />
        <ActiveInfo title="回访状态" value={data?.pro.FinanceData.visit_status} />
      </div>
    </Box>
  )
}

const PlanproDataTabs: React.FC<IProps> = (props) => {
  const { dispatch, user: { userData } } = props;
  const [type, setType] = useState<string>('');

  useEffect(() => {
    dispatch({
      type: 'user/fetchHealth',
      payload: {
        unionid: DecryptDes(GetCookie('unionid')),
      }
    }).then(res => {
      console.log(res);

    })
  }, [])

  const handleClick = (value: string) => {
    setType(value)
  }

  return (
    <>
      <div className={styles.headerBox}>
        <TabBtn options={Planpro_information} onChange={handleClick} />
      </div>
      {
        type ? type === 'basic_data' && <div className={styles.contentBox}>
          <BasicData data={userData} />
        </div> : <div className={styles.contentBox}>
            <BasicData data={userData} />
          </div>
      }
      {
        type ? type === 'health_questionnaire' && <div className={styles.contentBox}>
          <HealthQuestionnaire />
        </div> : <div className={styles.contentBox}>
            <HealthQuestionnaire />
          </div>
      }
      {
        type ? type === 'security_plan' && <div className={styles.contentBox}>
          <SecurityPlan data={userData} />
        </div> : <div className={styles.contentBox}>
            <SecurityPlan data={userData} />
          </div>
      }
      {
        type ? type === 'comprehensive_plan' && <div className={styles.contentBox}>
          <ComprehensivePlan data={userData} />
        </div> : <div className={styles.contentBox}>
            <ComprehensivePlan data={userData} />
          </div>
      }
    </>
  )
}
export default connect(({ user }: { user: UserState }) => ({
  user,
}))(PlanproDataTabs);