import React, { useState } from 'react';

import TabBtn from '../../components/TabBtn';
import ActiveInfo from '../../../../components/ActiveInfo';
import {
  server,
} from './datas';
import styles from './index.less';
import Box from '../../Boxs/Box';
import { connect } from 'dva';
import { UserState } from '../../model';

const Solid = ({ data }) => {
  return (
    <Box title="Wealth实操">
      <div>
        <ActiveInfo title="所在城市" value={data?.wealthService?.city} />
        <ActiveInfo title="客户年龄" value={data?.wealthService?.birth} />
        <ActiveInfo title="家庭年收入" value={data?.wealthService?.totalIncome} />
        <ActiveInfo title="年终结余" value={data?.wealthService?.rest} />
        <ActiveInfo title="流动资产" value={data?.wealthService?.floatAsset} />
        <ActiveInfo title="是否已婚" value={data?.wealthService?.floatAsset} />
        <ActiveInfo title="子女数量" value={data?.wealthService?.childNum} />
        <ActiveInfo title="跟进顾问" value={data?.wealthService?.userName} />
        <ActiveInfo title="首次跟进时间" value={data?.wealthService?.createDate} />
        <ActiveInfo title="报名来源" value='' />
      </div>
    </Box>
  )
}

const FamilyTarget = ({ data }) => {
  return (
    <Box title="家庭目标">
      <div className={styles.trainWealthQuestionCard}>
        <img src="http://wework.qpic.cn/bizmail/iajiakxs31goYHIFkIRm7g3GaniaH878bwcZmIiaXFIxYibBTic7GJGExDSw/0" alt="健康问卷" className={styles.left} />
        <div className={styles.right}>
          <div>先生 | <span>65岁退休</span></div>
          <div>1998-03-15</div>
        </div>
      </div>
      <div className={styles['family-target-bg']}>
        <ActiveInfo title="退休金目标" value="喜马拉雅市场部" />
        <ActiveInfo title="储蓄年期" value="喜马拉雅市场部" />
        <ActiveInfo title="实现年份" value="喜马拉雅市场部" />
      </div>
    </Box>
  )
}
interface IProps {
  user: UserState;
}

const ActiviteFollowUp: React.FC<IProps> = (props) => {
  const { user: { userData } } = props;
  const [type, setType] = useState<string>('');

  const handleClick = (value: string) => {
    console.log(value)
    setType(value)
  }

  return (
    <div>
      <div className={styles.headerBox}>
        <TabBtn options={server} onChange={handleClick} />
      </div>
      {
        type ? type === 'solid' && <div className={styles.contentBox}>
          <Solid data={userData.wealth} />
        </div> : <div className={styles.contentBox}>
            <Solid data={userData.wealth} />
          </div>
      }
      {
        type ? type === 'family_target' && <div className={styles.contentBox}>
          <FamilyTarget data={userData.wealth} />
        </div> : <div className={styles.contentBox}>
            <FamilyTarget data={userData.wealth} />
          </div>
      }
    </div>
  )
}

export default connect(({ user }: { user: UserState }) => ({
  user,
}))(ActiviteFollowUp);